.Box-wrap{
  display: flex;
  justify-content: center;
  gap: 1rem;
}
.Box{
  text-align: center;
  width: 30%;
  border: 10px solid #000;
  padding: 0 3rem; 
}
.main{
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  margin-top: 2rem;
}
.main button{
  cursor: pointer;
  border: none;
  background-color: #3e3896;
  color: #fff;
  font-size: 2rem;
  border-radius: 5px;
  transition: .3s;
}
.main button:hover{
  background-color: crimson;
}
.win{
  border: 10px solid green;
  color: green;
}
.lose{
  border: 10px solid red;
  color:  red;
}
.tie{
  border : 10px solid gray;
  color: gray;
}